@import '~assets/styles/h3Imports';

@mixin tablet-support {
  @media #{$tablet-up} {
    fieldset {
      > .day {
        margin-right: rem(4);
      }
    }
  }
}

@mixin desktop-support {
  @media #{$post-large-up} {
    html[data-fontsize='AAA'] & {
      fieldset > .day:last-child {
        margin-right: rem(25);
      }
    }

    .dropdown {
      max-width: rem(455);
      width: rem(455);
    }

    fieldset {
      padding: 0 rem(13);

      > .day {
        margin-right: rem(25);
      }
    }
  }
}

.root {
  display: flex;
  flex-direction: column;

  html[data-fontsize='AAA'] & {
    fieldset > .day:last-child {
      margin-right: rem(6);
    }
  }

  .dropdown {
    margin-bottom: rem(20);
    width: 100%;

    > :nth-child(2) > :last-child {
      height: rem(48);
      width: 100%;
    }
  }

  fieldset {
    border: none;
    display: flex;
    margin-right: rem(6);
    padding: 0 rem(2);

    .day {
      align-items: center;
      display: flex;
      flex-direction: column;
      grid-template-columns: none;
      justify-content: center;
      margin-right: rem(6);
      position: relative;

      > [data-input-control-element='label'] {
        align-items: center;
        bottom: rem(20);
        color: var(--accent-interactive, #284b9b);
        cursor: pointer;
        display: flex;
        font-size: rem(16);
        font-weight: 700;
        justify-items: center;
        nav-index: -1;
        position: absolute;
        top: 0;
      }

      &.is-checked > [data-input-control-element='label'] {
        color: white;
      }

      &.is-error > [data-input-control-element='label'] {
        color: var(--interface-error, #e10900);
      }
    }

    .day:last-child {
      margin-right: 0;
    }

    .day > :first-child {
      background-color: var(--white);
      height: rem(40);
      margin-bottom: rem(20);
      width: rem(40);

      &::before {
        box-shadow: unset;
      }

      > :first-child {
        border-radius: rem(20);
        height: rem(40);
        transition: var(--input-box-shadow-transition);
        width: rem(40);

        &:hover {
          box-shadow: 0 0 0 rem(8) rgba(50, 50, 100, 0.08);
        }
      }

      input[type='checkbox']::after {
        background: none;
      }

      &[data-checked='false'] > input[type='checkbox'][aria-invalid='true'] {
        border-color: var(--interface-error, #e10900);
      }
    }
  }

  .input-error {
    color: var(--interface-error);
    font-size: rem(14);
    font-weight: 600;
  }

  @include tablet-support;
  @include desktop-support;
}

@media #{$tablet-down} {
  html[data-fontsize='AA'] {
    fieldset {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-content: space-between;
    }
  }
}

html[data-fontsize='AAA'] {
  fieldset {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: space-between;
  }
}
